.dream{
  background-image: linear-gradient(to right, #434eff 42%, #af6aff 61%, #ffd66c 80%);
  
}
#imageleft{
  animation-name: moveleft;
  animation-duration: 20s;
  animation-delay: 0ms;
  animation-iteration-count: infinite;
  animation-direction: normal;
  animation-play-state: running;
}
@keyframes moveleft{
  0%{
    transform: translateX(0);
  }
  
  80%{
    transform: translateX(-1000px);
  }
  
}
#sha1{
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.603);
}

#bgimage{
  background-image: url('https://assets-global.website-files.com/64da0495a0c05cbac93130da/64dc73372e66522862d595dd_cta-pattern.svg');
  background-repeat: no-repeat;
  background-size: cover; 
  background-position: center;
}

#peace::after{
  content: "";
  display: block;
  z-index: -1;
  position: absolute;
  background-image: url("https://assets-global.website-files.com/64da0495a0c05cbac93130da/64db4b918003b5f97671f5b0_story-top-star-icon.svg");
  background-position: right 35%;
  background-position-x: 90%;
  
  background-repeat: no-repeat;
  background-size: 10%;
  width: 350px;
  height: 350px;
  background-color: #5e53fa;
  border-radius: 50%;
  left: 20%;
  top: 5%;
}
#peace::before{
  content: "";
  display: block;
  z-index: -1;
  position: absolute;
  background-image: url("https://assets-global.website-files.com/64da0495a0c05cbac93130da/64db4db8b130ef3addae1abb_story-bottom-star-icon.svg");
  background-position: left 70%;
  background-position-x: 5%;
  
  background-repeat: no-repeat;
  background-size: 15%;
  width: 350px;
  height: 350px;
  background-color: #ffce50;
  border-radius: 50%;
  left: 0;
  top: 50%;
}
@media screen and (max-width: 1000px) {
  #peace::after{
    content: "";
    display: block;
    z-index: -1;
    position: absolute;
    width: 260px;
    height: 260px;
    background-color: #5e53fa;
    border-radius: 50%;
    left: 13.5%;
    top: 5%;
  }

  #peace::before{
    content: "";
    display: block;
    z-index: -1;
    position: absolute;
    width: 250px;
    height: 250px;
    background-color: #ffce50;
    border-radius: 50%;
    left: 5%;
    top: 45%;
  }
}
@media screen and (max-width: 600px) {
  #peace::after{
    content: "";
    display: block;
    z-index: -1;
    position: absolute;
    width: 260px;
    height: 260px;
    background-color: #5e53fa;
    border-radius: 50%;
    left: 25%;
    top: 5%;
  }

  #peace::before{
    content: "";
    display: block;
    z-index: -1;
    position: absolute;
    width: 250px;
    height: 250px;
    background-color: #ffce50;
    border-radius: 50%;
    left: 0;
    top: 45%;
  }
}

